
table .img{
    background-repeat: no-repeat;
    background-position: 50%;
    max-height: 5rem;
}

tr{
    text-align: center;
    vertical-align: baseline;
    width: fit-content !important;
}

tr input  tr input:active {
    display: inline-block;
    text-align: center !important;
    border-radius: .2em;
    border: 1px solid;
}

.Basket .amount-btn{
    height: 1.5em;
    width: 1.5em;
    position: relative;
    border-radius: 50%;
    box-sizing: content-box;
    padding: 0;
}

table .price {
    font-weight: normal;
    font-size: inherit;
}

table .price::before{
    content: "";
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button{
    -webkit-appearance: none;
    margin: 0;
}